@import url(https://fonts.googleapis.com/css2?family=Urbanist:wght@400;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.popup-overlay {
  background: rgba(0, 0, 0, 0.5);
}

.popup-content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  padding: 20px;
  width: 80%;
  height: 40%;
  max-height: 300px;
  max-width: 600px;
  text-align: center;
}

.App {
  /* text-align: center; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Urbanist", sans-serif;
}

.trivia-container {
  margin-top: 20px;
  width: 50%;
  font-size: 25px;
}

.heading {
  text-align: center;
  font-size: 50px;
  margin-top: 20px;
}

.question {
  background: rgb(148, 233, 167);
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 1px 5px rgba(139, 132, 132, 0.5);
}

.answers {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  gap: 20px;
}

.answers div {
  padding: 20px;
  background: #75f2f6;
  border-radius: 5px;
  box-shadow: 0px 1px 5px rgba(139, 132, 13, 0.2);
}

.answers label {
  margin-left: 10px;
}

.question {
  margin-top: 20px;
}

.buttons {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.btn-primary {
  padding: 10px 20px 10px 20px;
  font-size: 20px;
  background: #2ea02e;
  box-shadow: 0px 1px 5px rgba(139, 132, 132, 0.5);
  outline: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: white;
  font-weight: 600;
}

.btn-primary:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0px 3px 8px rgba(139, 132, 132, 0.5);
}

.score {
  margin-top: 20px;
  font-size: 20px;
}

.play-again {
  margin-top: 20px;
}

[type="radio"]:checked,
[type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px;
}
[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label {
  position: relative;
  padding-left: 28px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  color: black;
}
[type="radio"]:checked + label:before,
[type="radio"]:not(:checked) + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  border: 1px solid #ddd;
  border-radius: 100%;
  background: #fff;
}
[type="radio"]:checked + label:after,
[type="radio"]:not(:checked) + label:after {
  content: "";
  width: 12px;
  height: 12px;
  background: rgb(20, 76, 167);
  position: absolute;
  top: 4px;
  left: 4px;
  border-radius: 100%;
  transition: all 0.2s ease;
}
[type="radio"]:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}
[type="radio"]:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

